.custom-image-swiper {
  width: 100%;
  position: relative;
  font-size: 0
}

.custom-image-swiper .swiper-wrapper {
  position: relative
}

.custom-image-swiper .swiper-wrapper:after {
  content: " ";
  display: block;
  padding-top: 48%
}

.custom-image-swiper .title {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: 0 auto;
  text-align: left;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 5px 15px;
  z-index: 3;
  background-color: rgba(51, 51, 51, .8);
  color: #fff;
  font-size: 14px;
  line-height: 1.5
}

.custom-image-swiper a[href="javascript:;"],
.custom-image-swiper a[href="javascript:void(0);"] {
  cursor: default
}

.custom-image-swiper .swp-page {
  position: absolute;
  top: 0;
  left: 0;
  text-align: center
}

.custom-image-swiper .swp-page img {
  max-width: 100%;
  max-height: 100%;
  width: 100%
}

.custom-image-swiper .swiper-slide a {
  display: block;
  position: relative;
  text-align: center;
  width: 320px
}

.custom-image-swiper .swiper-slide a img {
  max-width: 100%;
  height: auto
}

.custom-image-swiper-single .swiper-wrapper {
  position: relative
}

.custom-image-swiper-single .swiper-wrapper:after {
  content: " ";
  display: block;
  padding-top: 0
}

.custom-image-swiper-single .swp-page {
  position: static
}

.custom-image-swiper-single .swiper-slide {
  float: none
}

