.shops {
  background-color: #fff;
  margin-bottom: 10px;
  font-size: 0;
  position: relative
}

.shops:after {
  content: " ";
  display: block;
  padding-top: 58%
}

.shops:before {
  z-index: 1;
  content: "";
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-top: 1px solid #e5e5e5
}

.shop-wrap,
.shops:before {
  position: absolute;
  top: 0;
  left: 0
}

.shop-wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow: hidden
}

.shop-item {
  position: relative;
  display: inline-block;
  width: 33.333%
}

.shop-item:before {
  z-index: 1;
  content: "";
