  top: 0;
  left: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(.5);
  transform: scale(.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-right: 1px solid #e5e5e5
}

.shop-item:last-child:before {
  content: "";
  display: none
}

.shop-item img {
  position: absolute;
  margin: auto;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  max-width: 100%;
  max-height: 100%
